import '../../../App.css';
import * as React from "react"

import FC from "../../../data/img/FC.png";
import FM from "../../../data/img/FM.png";
import LS from "../../../data/img/LS.png";
import MO from "../../../data/img/MO.png";
import MOL from "../../../data/img/MOL.png";
import SP from "../../../data/img/SP.png";

import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import { Modal } from '@mui/material';
import Backdrop from '@mui/material/Backdrop';



const Boats = () => {


    const Item = styled(Paper)(({ theme }) => ({
        backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
        ...theme.typography.body2,
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    }));

    // const [open, setOpen] = React.useState(false);
    // const handleOpen = () => {
    //     setOpen(true);
    // };
    // const handleClose = () => {
    //     setOpen(false);
    // };

    const [open1, setOpen1] = React.useState(false);
    const [open2, setOpen2] = React.useState(false);
    const [open3, setOpen3] = React.useState(false);
    const [open4, setOpen4] = React.useState(false);
    const [open5, setOpen5] = React.useState(false);
    const [open6, setOpen6] = React.useState(false);


    const stylemodal = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 380,
        bgcolor: 'background.paper',
        border: '1px solid rgba(146, 145, 145, 0.4)',
        boxShadow: 0,
        p: 2,
    };


    return (
        <>
            <Box sx={{ width: '100%' }}>
                <Grid container rowSpacing={3} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>

                    <Grid item xs={6}>
                        <Item sx={{ height: '100%' }}>
                            <Button onClick={() => setOpen1(true)} className='boatmodal'>
                                <CardMedia
                                    component="img"
                                    alt="Le trimaran avec filet carrelet - FC"
                                    height="230"
                                    image={FC}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>LE TRIMARAN AVEC FILET CARRELET - FC<br />11 pcheuses // mailles 6-10 mm
                                    </Typography>
                                </CardContent>
                            </Button>
                            <Modal
                                aria-labelledby="transition-modal-title"
                                aria-describedby="transition-modal-description"
                                open={open1}
                                onClose={() => setOpen1(false)}
                                closeAfterTransition
                                slots={{ backdrop: Backdrop }}
                                slotProps={{
                                    backdrop: {
                                        timeout: 500,
                                    },
                                }}
                            >
                                <Box className="boatboxmodal" sx={stylemodal} style={{ backgroundColor: "rgba(247, 245, 251, 1)" }}>
                                    <CardMedia
                                        component="img"
                                        alt="Le trimaran avec filet carrelet - FC"
                                        height="300"
                                        image={FC}
                                        sx={{ objectFit: "contain" }} />
                                    <CardContent>
                                        <Typography component='div' className="boatdescr" color="text.secondary" gutterBottom>Cette technique utilise le filet carrelet appel  lift net , une nappe en fil de nylon multifilament, de forme carre, dune aire de 400 m2 avec des mailles de 5 et 6 mm de noeud  noeud. Au niveau des angles, sont attaches 4 longues cordes en nylon, de 3  4 cm de diamtre. Elle emploie 3 pirogues ( trimaran ) non motorises spares mais attaches entre elles par deux longues perches appeles  rails  et quatre autres perches appeles  pol  pour relier les aussires attaches aux extrmits du lift net lors de la pche. Quatre lampes sont portes par la pirogue du milieu et tout le systme est assist par une pirogue motorise, petite et indpendante, nomme  renfort . La pche au trimaran est une technique de pche utilise au lac Kivu avec un systme dattraction lumineux. En effet, les poissons sont attirs dans le filet  laide de lclairage gnr par 4 lampes de marque Coleman ou  Anchor  qui restent allumes pendant toute lactivit de pche  lexception des moments de relvement du filet. Lembarquement de lquipage, compos de 11 pcheurs dont 2 professionnels et 9 autres occasionnels, commence aux environs de 18h. Une fois sur la zone de pche, en milieu plagique, le trimaran est orient dans la direction du vent. Les pcheurs jettent lancre, puis procdent au premier jet du filet pour ensuite allumer les lampes. Le premier relvement intervient  21h, le deuxime  23h30, le troisime  2h, et le quatrime parfois vers 4h30 du matin. Lors du relvement, le filet est sorti de leau sous forme de poche conique avec louverture tourne vers le haut.
                                        </Typography>
                                    </CardContent>
                                </Box>
                            </Modal>
                        </Item>
                    </Grid>

                    <Grid item xs={6}>
                        <Item sx={{ height: '100%' }}>
                            <Button onClick={() => setOpen2(true)} className='boatmodal'>
                                <CardMedia
                                    component="img"
                                    alt="La senne de plage - SP"
                                    height="230"
                                    image={SP}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>LA SENNE DE PLAGE - <span style={{ borderWidth: 3, borderColor: "#2e2a7a", borderStyle: "solid" }}>SP</span><br />2-5 pcheuses // mailles 4-8 mm
                                    </Typography>
                                </CardContent>
                            </Button>
                            <Modal
                                aria-labelledby="transition-modal-title"
                                aria-describedby="transition-modal-description"
                                open={open2}
                                onClose={() => setOpen2(false)}
                                closeAfterTransition
                                slots={{ backdrop: Backdrop }}
                                slotProps={{
                                    backdrop: {
                                        timeout: 500,
                                    },
                                }}
                            >
                                <Box className="boatboxmodal" sx={stylemodal} style={{ backgroundColor: "rgba(247, 245, 251, 1)" }}>
                                    <CardMedia
                                        component="img"
                                        alt="La senne de plage - SP"
                                        height="300"
                                        image={SP}
                                        sx={{ objectFit: "contain" }} />
                                    <CardContent>
                                        <Typography component='div' className="boatdescr" color="text.secondary" gutterBottom>La pche  la senne de plage se pratique gnralement  partir dune pirogue monoxyle de 4  5 m de longueur. De forme rectangulaire et attache  des longues cordes (aussires) de halage de chaque ct, elle est forme dune nappe en fil de nylon multifilament, souvent associe aux nappes des carrelets uss. Sa dimension est de quelque trentaine de mtres de longueur et de quelque dizaine de mtres de chute, les mailles variant entre 4 mm et 8 mm dentre noeuds. La ralingue suprieure du filet est munie des flotteurs tandis que la ralingue infrieure est leste au moyen soit des morceaux de briques en terre cuite, soit des cailloux ou mme des morceaux des barres de fer. Lquipage, compos de 2  5 pcheurs y compris le propritaire et de son aide-pcheur, exerce lactivit essentiellement en zone littorale  partir de 4h du matin jusqu 10h (au plus tard). Lors de lactivit, lune des aussires attaches aux extrmits du filet est tenue par un pcheur se trouvant sur la plage tandis que les autres,  laide de la pirogue sembarquent avec lautre partie de laussire quils droulent et jettent dans leau, chaque fois en avanant vers le large du lac. Ils font un arc de cercle et regagnent la plage pour tirer le filet par les bouts des aussires tout en balayant le substrat. La dure de lactivit est trs variable, allant dune demi-heure  cinq heures par sortie et par unit de pche.
                                        </Typography>
                                    </CardContent>
                                </Box>
                            </Modal>
                        </Item>
                    </Grid>

                    <Grid item xs={6}>
                        <Item sx={{ height: '100%' }}>
                            <Button onClick={() => setOpen3(true)} className='boatmodal'>
                                <CardMedia
                                    component="img"
                                    alt="Les filets maillants - FM"
                                    height="150"
                                    image={FM}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>LES FILETS MAILLANTS - <span style={{ borderWidth: 3, borderColor: "#df8f44", borderStyle: "solid" }}>FM</span><br />3-4 pcheuses // mailles 20-30 mm
                                    </Typography>
                                </CardContent>
                            </Button>
                            <Modal
                                aria-labelledby="transition-modal-title"
                                aria-describedby="transition-modal-description"
                                open={open3}
                                onClose={() => setOpen3(false)}
                                closeAfterTransition
                                slots={{ backdrop: Backdrop }}
                                slotProps={{
                                    backdrop: {
                                        timeout: 500,
                                    },
                                }}
                            >
                                <Box className="boatboxmodal" sx={stylemodal} style={{ backgroundColor: "rgba(247, 245, 251, 1)" }}>
                                    <CardMedia
                                        component="img"
                                        alt="Les filets maillants - FM"
                                        height="300"
                                        image={FM}
                                        sx={{ objectFit: "contain" }} />
                                    <CardContent>
                                        <Typography component='div' className="boatdescr" color="text.secondary" gutterBottom>Les filets maillants sont constitus par une nappe rectangulaire en fil de nylon monofilament, dont la longueur varie entre 60 et 180 m et la largeur varie entre 4 et 12 m. La taille des mailles varie entre 6 et 10 mm dentre noeuds. Il nest pas exclu de rencontrer occasionnellement des filets  grandes mailles pouvant atteindre 20  30 mm, destins  la capture des poissons de grande taille, tels que les tilapias. La nappe de filet est monte sur deux ralingues, une suprieure munie de flotteurs et une autre infrieure munie de lest, soit des morceaux de plomb, ou de pierre. Au lac Kivu, la tendance est  la diversification des techniques dexploitation des ressources au filet maillant, partant de la pche  la frappe  lusage des petites clochettes pour inciter les poissons  la mobilit, ou bien mme  lusage des petites torches accroches au filet et restant allumes durant le temps dimmersion pour lattrait des poissons chasseurs  vue. La pche au filet maillant se pratique  la tombe de la nuit au lever du jour. Un quipage se compose de 3  4 pcheurs dont un est professionnel et les autres sont occasionnels, utilisant une pirogue en planche non motorise de 3  5 m de long, 2 cordes attaches au filet et 3  4 rames en bois. Les pcheurs pratiquent leurs activits de pche entre 17h et 18h prs du littoral ou dans les eaux plagiques, tout en tenant compte des mouvements des vents. Les filets sont poss vers 18h30 et relevs aux environs de 21h. Les pcheurs retournent soit directement ou le lendemain sur la plage de dbarquement pour commencer le dmaillage qui peut prendre plusieurs heures par filet en cas de bonne capture. La pche matinale se fait  partir de 3h30. Les filets sont relevs vers 6h du matin et le dmaillage a lieu entre 7h et 8h, heures auxquelles les mareyeurs viennent acheter les produits frais de la pche pour les revendre soit sur la plage ou aux marchs locaux.
                                        </Typography>
                                    </CardContent>
                                </Box>
                            </Modal>
                        </Item>
                    </Grid>

                    <Grid item xs={2}>
                        <Item sx={{ height: '100%' }}>
                            <Button onClick={() => setOpen4(true)} className='boatmodal'>
                                <CardMedia
                                    component="img"
                                    alt="La ligne simple - LS"
                                    height="150"
                                    image={LS}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>LA LIGNE SIMPLE - <span style={{ borderWidth: 3, borderColor: "#cb4d37", borderStyle: "solid" }}>LS</span><br />1 pcheuses
                                    </Typography>
                                </CardContent>
                            </Button>
                            <Modal
                                aria-labelledby="transition-modal-title"
                                aria-describedby="transition-modal-description"
                                open={open4}
                                onClose={() => setOpen4(false)}
                                closeAfterTransition
                                slots={{ backdrop: Backdrop }}
                                slotProps={{
                                    backdrop: {
                                        timeout: 500,
                                    },
                                }}
                            >
                                <Box className="boatboxmodal" sx={stylemodal} style={{ backgroundColor: "rgba(247, 245, 251, 1)" }}>
                                    <CardMedia
                                        component="img"
                                        alt="La ligne simple - LS"
                                        height="300"
                                        image={LS}
                                        sx={{ objectFit: "contain" }} />
                                    <CardContent>
                                        <Typography component='div' className="boatdescr" color="text.secondary" gutterBottom>La ligne simple, couramment appele  hameon , elle se compose dun fil en nylon (monofilament) de longueur variant entre 3 et 6 m, attach  une canne mince et flexible, et muni dun seul ou de 3 hameons au plus. La mthode consiste  jeter la ligne dans leau et  remonter celle-ci petit  petit  une mme vitesse. La pche  la ligne simple est galement utilise par les enfants le long de berges, les lignes sont souvent laisses dans leau jusqu la capture dun poisson.
                                        </Typography>
                                    </CardContent>
                                </Box>
                            </Modal>
                        </Item>
                    </Grid>

                    <Grid item xs={2}>
                        <Item sx={{ height: '100%' }}>
                            <Button onClick={() => setOpen5(true)} className='boatmodal'>
                                <CardMedia
                                    component="img"
                                    alt="Le tulle moustiquaire - MO"
                                    height="150"
                                    image={MO}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>LE TULLE MOUSTIQUAIRE - <span style={{ borderWidth: 3, borderColor: "#c53c6a", borderStyle: "solid" }}>Mo</span><br />2 pcheuses // mailles 0.3-1.7 mm // <span style={{ borderWidth: 1, borderColor: "#c53c6a", borderStyle: "solid" }}>identifi comme illicite</span>
                                    </Typography>
                                </CardContent>
                            </Button>
                            <Modal
                                aria-labelledby="transition-modal-title"
                                aria-describedby="transition-modal-description"
                                open={open5}
                                onClose={() => setOpen5(false)}
                                closeAfterTransition
                                slots={{ backdrop: Backdrop }}
                                slotProps={{
                                    backdrop: {
                                        timeout: 500,
                                    },
                                }}
                            >
                                <Box className="boatboxmodal" sx={stylemodal} style={{ backgroundColor: "rgba(247, 245, 251, 1)" }}>
                                    <CardMedia
                                        component="img"
                                        alt="Le tulle moustiquaire - MO"
                                        height="300"
                                        image={MO}
                                        sx={{ objectFit: "contain" }} />
                                    <CardContent>
                                        <Typography component='div' className="boatdescr" color="text.secondary" gutterBottom>Cest une nappe moustiquaire de forme rectangulaire, de 0,3  1,7 mm de mailles, faisant 2  4 m de long et 1  2 m de chute. Cet engin est souvent utilis clandestinement car identifi comme pche illicite au lac Kivu. La technique est pratique  une profondeur de moins de 2 m le long des berges colonises par des plantes aquatiques non ligneuses. Vers 5h du matin, les 2 pcheurs se trouvent dans leau et tirent ensemble le filet vers la cte en touchant lgrement le substrat, les quatre angles de lengin tant fortement tenus, chacun par une main.
                                        </Typography>
                                    </CardContent>
                                </Box>
                            </Modal>
                        </Item>
                    </Grid>

                    <Grid item xs={2}>
                        <Item sx={{ height: '100%' }}>
                            <Button onClick={() => setOpen6(true)} className='boatmodal'>
                                <CardMedia
                                    component="img"
                                    alt="La moustiquaire associe au Lusenga -MOL"
                                    height="150"
                                    image={MOL}
                                    sx={{ objectFit: "contain" }} />
                                <CardContent>
                                    <Typography component='div' className="fish" color="text.secondary" gutterBottom>LA MOUSTIQUAIRE ASSOCIE AU LUSENGA - <span style={{ borderWidth: 3, borderColor: "#2b5f43", borderStyle: "solid" }}>MoL</span><br />2 pcheuses // <span style={{ borderWidth: 1, borderColor: "#c53c6a", borderStyle: "solid" }}>identifi comme illcite</span>
                                    </Typography>
                                </CardContent>
                            </Button>
                            <Modal
                                aria-labelledby="transition-modal-title"
                                aria-describedby="transition-modal-description"
                                open={open6}
                                onClose={() => setOpen6(false)}
                                closeAfterTransition
                                slots={{ backdrop: Backdrop }}
                                slotProps={{
                                    backdrop: {
                                        timeout: 500,
                                    },
                                }}
                            >
                                <Box className="boatboxmodal" sx={stylemodal} style={{ backgroundColor: "rgba(247, 245, 251, 1)" }}>
                                    <CardMedia
                                        component="img"
                                        alt="La moustiquaire associe au Lusenga -MOL"
                                        height="300"
                                        image={MOL}
                                        sx={{ objectFit: "contain" }} />
                                    <CardContent>
                                        <Typography component='div' className="boatdescr" color="text.secondary" gutterBottom>La moustiquaire associe au Lusenga est un engin de forme rectangulaire, dune superficie moyenne de 250 m2, soit 25 m de longueur sur 10 m de largeur. Sa fabrication demeure archaque et consiste en une couture plaant clandestinement dans la partie centrale dun filet carrelet us (localement appel  Lusenga ), un tulle moustiquaire  dimensions trs variables. Lengin ainsi fabriqu, les quatre angles sont relis chacun  une corde en nylon dont se servent les pcheurs, souvent  deux pour tirer le filet de leau, formant un arc comme celui de la senne de plage. Lactivit se passe dans la zone littorale et se droule tt le matin pour une dure maximale denviron 1h avec possibilit des rptitions.
                                        </Typography>
                                    </CardContent>
                                </Box>
                            </Modal>
                        </Item>
                    </Grid>

                </Grid>
            </Box></>
    )
}


export default Boats;